<template>
    <div class="zuinankemu">
        <div class="kemu-header">
            <div class="title"></div>
            <select id="select" v-model="selected">
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option>/</option>
                <option></option>
                <option></option>
                <option>/</option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
                <option></option>
            </select>
        </div>
        <div id="kemu-container"></div>
    </div>
</template>

<script>
    var echarts = require('echarts/lib/echarts')
    require('echarts/lib/chart/bar')
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')
    require("echarts/lib/component/legendScroll")

    export default {
        data() {
            return {
                selected: '',
                subject: ['A1', '', 'A'],
                data: [0.37, 0.32, 0.31]
            }
        },
        methods: {
            draw() {
                let myChart = echarts.init(document.getElementById('kemu-container'))
                myChart.setOption({
                    xAxis: {
                        type: 'category',
                        data: this.subject,
                        axisLine: {
                            lineStyle: {
                                color: '#432a92',
                                width: 4
                            }
                        },
                        axisLabel: {
                            fontSize: 14,
                            fontFamily: 'Microsoft YaHei',
                            fontWeight: 350,
                            rotate: 12
                        },
                        name: '',
                        nameTextStyle: {
                            fontSize: 14
                        }
                    },
                    yAxis: {
                        type: 'value',
                        axisLine: {
                            lineStyle: {
                                color: '#432a92',
                                width: 4
                            }
                        },
                        axisLabel: {
                            fontSize: 20,
                            fontFamily: 'Microsoft YaHei',
                            fontWeight: 350
                        },
                        splitLine: {
                            show: false
                        },
                        name: '60',
                        nameTextStyle: {
                            fontSize: 14
                        }
                    },
                    series: [{
                        data: this.data,
                        type: 'bar',
                        color: function (params) {
                            var colorList = ['#ffbff9', '#9ce8ff', '#94f8f4'];
                            return colorList[params.dataIndex]
                        },
                        barWidth: 100,
                        itemStyle: {
                            normal: {
                                barBorderRadius: [10, 10, 0, 0],
                                barBorderColor: '#432a92',
                                barBorderWidth: 4,
                            },
                            emphasis: {
                                shadowBlur: 20,
                                shadowColor: 'rgba(0, 0, 0, 0.3)',
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                textStyle: {
                                    color: '#432a92',
                                    fontSize: 20,
                                    fontFamily: 'Microsoft YaHei',
                                    fontWeight: 350
                                }
                            }
                        }
                    }],
                    tooltip: {
                        formatter: "{b} : {c}"
                    }
                })
            }
        },
        mounted() {
            this.draw()
        },
        watch: {
            selected() {
                if (this.selected === '') {
                    this.subject = ['A1', '', 'A']
                    this.data = [0.37, 0.32, 0.31]
                }
                else if (this.selected === '') {
                    this.subject = ['(2)1', '', '']
                    this.data = [0.52, 0.30, 0.17]
                }
                else if (this.selected === '') {
                    this.subject = ['B', 'A', 'C']
                    this.data = [0.63, 0.26, 0.12]
                }
                else if (this.selected === '') {
                    this.subject = ['B', 'A', ' (2)']
                    this.data = [0.59, 0.28, 0.13]
                }
                else if (this.selected === '/') {
                    this.subject = ['C', 'B', 'B']
                    this.data = [0.40, 0.33, 0.27]
                }
                else if (this.selected === '') {
                    this.subject = ['B', 'A', '']
                    this.data = [0.55, 0.31, 0.14]
                }
                else if (this.selected === '') {
                    this.subject = ['', 'B1', 'A2']
                    this.data = [0.44, 0.31, 0.24]
                }
                else if (this.selected === '/') {
                    this.subject = ['C', 'A', 'A']
                    this.data = [0.40, 0.36, 0.23]
                }
                else if (this.selected === '') {
                    this.subject = ['A', 'A', 'C']
                    this.data = [0.38, 0.35, 0.27]
                }
                else if (this.selected === '') {
                    this.subject = ['', '', '']
                    this.data = [0.65, 0.20, 0.15]
                }
                else if (this.selected === '') {
                    this.subject = ['B', 'A', '']
                    this.data = [0.60, 0.28, 0.12]
                }
                else if (this.selected === '') {
                    this.subject = ['', '', '']
                    this.data = [0.38, 0.38, 0.24]
                }
                else if (this.selected === '') {
                    this.subject = ['B', 'A', 'C']
                    this.data = [0.59, 0.27, 0.14]
                }
                else if (this.selected === '') {
                    this.subject = ['A', 'B', 'B']
                    this.data = [0.51, 0.36, 0.13]
                }
                this.draw()
            }
        }
    }
</script>

<style lang="less" scoped>
    .zuinankemu {
        width: 100%;
        height: 100%;
    }

    .kemu-header {
        width: 80%;
        height: 100px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    #kemu-container {
        width: 85%;
        height: 480px;
        margin: 0 auto;
    }

    .title {
        width: 140px;
        height: 27px;
        background: url('../../assets/tzuinankemu.png') no-repeat;
        background-size: 100% 100%;
    }
    #select {
        width: 270px;
        height: 40px;
        border: solid 3px #432a92;
        outline: none;
        border-radius: 5px;
        border-right: none;
        font-size: 14px;
        padding-left: 10px;
        color: #331f75;
        background:url("../../assets/select.png") no-repeat scroll right center #d5fbff;
        cursor: pointer;
        option {
            color: #331f75;
        }
        /*select*/
        appearance:none;
        -moz-appearance:none;
        -webkit-appearance:none;
        -ms-appearance:none;
    }
</style>